<h1 id="android-views-and-layouts" tabindex="-1"><a class="header-anchor" href="#android-views-and-layouts" aria-hidden="true">#</a> Android Views and Layouts</h1>
<p>Here's a short tutorial on how to create basic graphical user interfaces in Android Studio.</p>
<iframe width="560" height="314" src="https://www.youtube.com/embed/nJ-9jQw69SU" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
<h2 id="views" tabindex="-1"><a class="header-anchor" href="#views" aria-hidden="true">#</a> Views</h2>
<p>The graphical user interface (GUI) used in Android consists of <em>views</em>. The <code>View</code> class represents a general view, and sub-classes to <code>View</code> represent specific views, for example:</p>
<ul>
<li>The sub-class <code>Button</code> represents a view displaying a button the user can click on</li>
<li>The sub-class <code>TextView</code> represents a view displaying some text</li>
<li>The sub-class <code>ImageView</code> represents a view displaying an image</li>
<li>Etc.</li>
</ul>
<p>The GUI can be specified either:</p>
<ul>
<li>Programmatically, in which case you create new instances of the <code>View</code> classes you need to use yourself in the programming language you use (Java/Kotlin/whatever)</li>
<li>Declaratively in XML, in which case you can use functionality from the Android framework to create the <code>View</code> instances you need based on the content of an XML file</li>
</ul>
<p>Most often you specify the GUI in an XML file, since that's usually easier to read and write. This is especially true when the GUI consists of many <code>View</code>s. XML files specifying what the GUI should look like are put in the folder <code>res/layout/</code>, and named <code>in_this_convention.xml</code>.</p>
<div class="custom-container warning"><p class="custom-container-title">About the context</p>
<p>Most of the constructors in classes from the Android framework has a parameter called <code>context</code>. Here you should pass an instance of the <code>Context</code> class. The <code>Context</code> class keeps track of information the classes needs to function properly, such as which preferred language the user has selected on the device. Whenever you write your own code making use of these classes, the framework will provide you with a reference to a <code>Context</code> you can use. For example, the <code>Activity</code> class inherits from <code>Context</code>, so you can use an <code>Activity</code> as the context.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example of how to create a new <code>TextView</code> programmatically in Kotlin.</p>
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token comment">// Import the classes we need from the framework.</span>
<span class="token keyword">import</span> android<span class="token punctuation">.</span>widget<span class="token punctuation">.</span>TextView

<span class="token comment">// Find a context, e.g. the Activity this code runs in.</span>
<span class="token keyword">val</span> aContext <span class="token operator">=</span> theActivity <span class="token comment">// (does not work as it is, but imagine)</span>

<span class="token comment">// Create and configure the TextView.</span>
<span class="token keyword">val</span> myTextView <span class="token operator">=</span> <span class="token function">TextView</span><span class="token punctuation">(</span>aContext<span class="token punctuation">)</span>
myTextView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"This is the text the TextView should show!"</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div></div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example of how to create a new <code>TextView</code> declaratively in XML and Kotlin.</p>
<CodeGroup>
<CodeGroupItem title="res/layout/my_view.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token comment">&lt;!-- The URL-thing looks strange, but it simply defines the
     the "android" namespace, which most attributes comes from --></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>This is the text the TextView should show!<span class="token punctuation">"</span></span>
<span class="token punctuation">/></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="Kotlin code">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token comment">// Import the classes we need from the framework.</span>
<span class="token keyword">import</span> android<span class="token punctuation">.</span>widget<span class="token punctuation">.</span>TextView
<span class="token keyword">import</span> android<span class="token punctuation">.</span>view<span class="token punctuation">.</span>LayoutInflater

<span class="token comment">// Find a context, e.g. the Activity this code runs in.</span>
<span class="token keyword">val</span> aContext <span class="token operator">=</span> theActivity <span class="token comment">// (does not work as it is, but imagine)</span>

<span class="token comment">// Obtain a layoutInflater.</span>
<span class="token keyword">val</span> layoutInflater <span class="token operator">=</span> LayoutInflater<span class="token punctuation">.</span><span class="token function">from</span><span class="token punctuation">(</span>aContext<span class="token punctuation">)</span>

<span class="token comment">// Use the layoutInflater to convert the XML structure to View objects.</span>
<span class="token keyword">val</span> myTextView <span class="token operator">=</span> layoutInflater<span class="token punctuation">.</span><span class="token function">inflate</span><span class="token punctuation">(</span>
	R<span class="token punctuation">.</span>layout<span class="token punctuation">.</span>my_view<span class="token punctuation">,</span> <span class="token comment">// This is an identifier to the XML file to use.</span>
	<span class="token keyword">null</span> <span class="token comment">// This should be a reference to View the inflated View will be added to, but in this case we don't have that.</span>
<span class="token punctuation">)</span> <span class="token keyword">as</span> TextView <span class="token comment">// The inflate method always returns a general View, but we know that we will always get back a TextView when we use R.layout.my_view, so can safely downcast here.</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br></div></div></CodeGroupItem>
</CodeGroup>
</div>
<h2 id="layouts" tabindex="-1"><a class="header-anchor" href="#layouts" aria-hidden="true">#</a> Layouts</h2>
<p>The mentioned views (<code>Button</code>, <code>TextView</code>, <code>ImageView</code>) display something on the screen the user can see. Another category of views that exists is called <em>layouts</em>. Layouts don't display anything on the screen, but they are instead designed to contain other views (called <em>children</em>), and a layout is used to position its children. Example of layouts are:</p>
<ul>
<li>The class <code>LinearLayout</code>, which positions it's children next to each other, either horizontally or vertically</li>
<li>The class <code>FrameLayout</code>, which positions it's children independent of each other (the children can overlap)</li>
<li>The class <code>RelativeLayout</code>, which positions it's children relative to each other or relative to the layout (above, or below, or to the right of, or to the left of, etc.)</li>
<li>Etc.</li>
</ul>
<h2 id="layoutparams" tabindex="-1"><a class="header-anchor" href="#layoutparams" aria-hidden="true">#</a> LayoutParams</h2>
<p>When a view is used inside a layout, you need to add information to the view telling the layout how the view should be positioned. This information is represented through a class called <code>LayoutParams</code>. In this class, a view can indicate how tall and wide it wants to be. If a layout needs more information than that (which usually is the case), the layout can define a sub-class to <code>LayoutParams</code> the child should use. For example:</p>
<ul>
<li>Views in a <code>LinearLayout</code> should use the class <code>LinearLayout.LayoutParams</code></li>
<li>Views in a <code>FrameLayout</code> should use the class <code>FrameLayout.LayoutParams</code></li>
<li>Views in a <code>RelativeLayout</code> should use the class <code>RelativeLayout.LayoutParams</code></li>
<li>Etc.</li>
</ul>
<p>So, when using a <code>View</code>, you do not only specify the properties of the <code>View</code> itself, but also the properties of the <code>LayoutParams</code> corresponding to the layout the <code>View</code> will be used in, and these properties will differ depending on which layout you use.</p>
<p>The width and height of a view can be defined in <code>LayoutParams</code> through the two constants <code>wrap_content</code> and <code>match_parent</code>:</p>
<ul>
<li>If the width in <code>LayoutParams</code> is set to <code>wrap_content</code>, the width of the <code>View</code> will just be big enough to surround its content</li>
<li>If the width in <code>LayoutParams</code> is set to <code>match_parent</code>, the width of the <code>View</code> will be as wide as the layout</li>
<li>If the height in <code>LayoutParams</code> is set to <code>wrap_content</code>, the height of the <code>View</code> will just be big enough to surround its content</li>
<li>If the height in <code>LayoutParams</code> is set to <code>match_parent</code>, the height of the <code>View</code> will be as tall as the layout</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example of how to create a new <code>FrameLayout</code> containing two <code>TextView</code>s programmatically in Kotlin.</p>
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token comment">// Import the classes we need from the framework.</span>
<span class="token keyword">import</span> android<span class="token punctuation">.</span>widget<span class="token punctuation">.</span>TextView
<span class="token keyword">import</span> android<span class="token punctuation">.</span>widget<span class="token punctuation">.</span>FrameLayout
<span class="token keyword">import</span> android<span class="token punctuation">.</span>view<span class="token punctuation">.</span>Gravity
<span class="token keyword">import</span> android<span class="token punctuation">.</span>view<span class="token punctuation">.</span>ViewGroup

<span class="token comment">// Find a context, e.g. the Activity this code runs in.</span>
<span class="token keyword">val</span> aContext <span class="token operator">=</span> theActivity

<span class="token comment">// Create the GUI!.</span>
<span class="token keyword">val</span> theLayout <span class="token operator">=</span> <span class="token function">FrameLayout</span><span class="token punctuation">(</span>aContext<span class="token punctuation">)</span>

<span class="token keyword">val</span> theFirstTextView <span class="token operator">=</span> <span class="token function">TextView</span><span class="token punctuation">(</span>aContext<span class="token punctuation">)</span>
theFirstTextView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"I'm the first text view!"</span>
theFirstTextView<span class="token punctuation">.</span>layoutParams <span class="token operator">=</span> FrameLayout<span class="token punctuation">.</span><span class="token function">LayoutParams</span><span class="token punctuation">(</span>
	ViewGroup<span class="token punctuation">.</span>LayoutParams<span class="token punctuation">.</span>MATCH_PARENT<span class="token punctuation">,</span> <span class="token comment">// The width the view should have.</span>
	ViewGroup<span class="token punctuation">.</span>LayoutParams<span class="token punctuation">.</span>WRAP_CONTENT<span class="token punctuation">,</span> <span class="token comment">// The height the view should have.</span>
	Gravity<span class="token punctuation">.</span>CENTER <span class="token comment">// The "gravity" the view should have (where it should be positioned in the FrameLayout).</span>
<span class="token punctuation">)</span>
theLayout<span class="token punctuation">.</span><span class="token function">addView</span><span class="token punctuation">(</span>theFirstTextView<span class="token punctuation">)</span>

<span class="token keyword">val</span> theSecondTextView <span class="token operator">=</span> <span class="token function">TextView</span><span class="token punctuation">(</span>aContext<span class="token punctuation">)</span>
theSecondTextView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"I'm the second text view!"</span>
theSecondTextView<span class="token punctuation">.</span>layoutParams <span class="token operator">=</span> FrameLayout<span class="token punctuation">.</span><span class="token function">LayoutParams</span><span class="token punctuation">(</span>
	ViewGroup<span class="token punctuation">.</span>LayoutParams<span class="token punctuation">.</span>WRAP_CONTENT<span class="token punctuation">,</span>
	ViewGroup<span class="token punctuation">.</span>LayoutParams<span class="token punctuation">.</span>WRAP_CONTENT<span class="token punctuation">,</span>
	Gravity<span class="token punctuation">.</span>BOTTOM
<span class="token punctuation">)</span>
theLayout<span class="token punctuation">.</span><span class="token function">addView</span><span class="token punctuation">(</span>theSecondTextView<span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br></div></div><p>The GUI would look something like this:</p>
<Smartphone>
	<div style="position: relative; height: 100%">
		<div style="position: absolute; width: 100%; top: 50%; transform: translateY(-50%);">I'm the first text view!</div>
		<div style="position: absolute; bottom: 0;">I'm the second text view!</div>
	</div>
</Smartphone>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example of how to create a new <code>TextView</code> declaratively in XML and Kotlin.</p>
<CodeGroup>
<CodeGroupItem title="res/layout/my_view.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>FrameLayout</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
<span class="token punctuation">></span></span>
	
	<span class="token comment">&lt;!-- All attributes starting with "layout_" are used in the
	     LayoutParams, so which "layout_*" attributes you can use
	     depends on which Layout you are using. --></span>
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_gravity</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>center<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>I<span class="token punctuation">'</span>m the first text view!<span class="token punctuation">"</span></span>
	<span class="token punctuation">/></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_gravity</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>bottom<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>I<span class="token punctuation">'</span>m the second text view!<span class="token punctuation">"</span></span>
	<span class="token punctuation">/></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>FrameLayout</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="Kotlin code">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">import</span> android<span class="token punctuation">.</span>widget<span class="token punctuation">.</span>FrameLayout
<span class="token keyword">import</span> android<span class="token punctuation">.</span>view<span class="token punctuation">.</span>LayoutInflater

<span class="token keyword">val</span> aContext <span class="token operator">=</span> theActivity

<span class="token keyword">val</span> layoutInflater <span class="token operator">=</span> LayoutInflater<span class="token punctuation">.</span><span class="token function">from</span><span class="token punctuation">(</span>aContext<span class="token punctuation">)</span>

<span class="token keyword">val</span> myFrameLayout <span class="token operator">=</span> layoutInflater<span class="token punctuation">.</span><span class="token function">inflate</span><span class="token punctuation">(</span>
	R<span class="token punctuation">.</span>layout<span class="token punctuation">.</span>my_view<span class="token punctuation">,</span>
	<span class="token keyword">null</span>
<span class="token punctuation">)</span> <span class="token keyword">as</span> FrameLayout <span class="token comment">// This time we know we will get back a FrameLayout, since that's the root element in the XML file.</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></CodeGroupItem>
</CodeGroup>
<p>The GUI would look something like this:</p>
<Smartphone>
	<div style="position: relative; height: 100%">
		<div style="position: absolute; width: 100%; top: 50%; transform: translateY(-50%);">I'm the first text view!</div>
		<div style="position: absolute; bottom: 0;">I'm the second text view!</div>
	</div>
</Smartphone>
