<h2 id="edittexts" tabindex="-1"><a class="header-anchor" href="#edittexts" aria-hidden="true">#</a> EditTexts</h2>
<p>The view <code>EditText</code> represents a field into which the user can enter some data. The property <code>inputType</code> indicates what type of data the user can enter:</p>
<ul>
<li>The value <code>text</code> means the user should enter a text</li>
<li>The value <code>number</code> means the user should enter a number</li>
<li>The value <code>textEmailAddress</code> means the user should enter an email address</li>
<li>The value <code>date</code> means the user should enter a date</li>
<li>Etc.</li>
</ul>
<p>When the user clicks on the <code>EditText</code>, a virtual keyboard will be shown on the screen, with keys the user can click to type characters into the <code>EditText</code>. Which keys they virtual keyboard shows depends on which <code>inputType</code> the <code>EditText</code> has. For example, if <code>inputType=&quot;number&quot;</code>, mostly numeric keys are shown. Precisely what it will look like depends on which virtual keyboard the user has installed and is using.</p>
<p>When you have a reference to an <code>EditText</code>, you can read out what the user has typed in it by using <code>theEditText.text.toString()</code>.</p>
<p>To listen for when the text in the <code>EditText</code> is changing, call the method <code>addTextChangedListener()</code>, and pass it an instance of a class implementing the interface <code>TextWatcher</code>. This interface defines three methods:</p>
<ul>
<li>The <code>beforeTextChanged()</code> method is called before the user's change to the text has been applied, and the arguments indicate which change that will be applied to the text shortly</li>
<li>The <code>onTextChanged()</code> method is called after the user's change to the text has been applied, and the arguments indicates which changes to the text was applied</li>
<li>The <code>afterTextChanged()</code> method is called after the user's change to the text has been applied, and its single argument indicates what the text looks like now</li>
</ul>
<p>In most cases, you can simply use <code>afterTextChanged()</code>.</p>
<p>The <code>hint</code> property can be used to display a text in the <code>EditText</code> when it's empty.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how to mirror what the user has typed in an <code>EditText</code> in a <code>TextView</code>.</p>
