<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">import</span> android<span class="token punctuation">.</span>app<span class="token punctuation">.</span>Activity
<span class="token keyword">import</span> android<span class="token punctuation">.</span>os<span class="token punctuation">.</span>Bundle
<span class="token keyword">import</span> android<span class="token punctuation">.</span>text<span class="token punctuation">.</span>TextWatcher
<span class="token keyword">import</span> android<span class="token punctuation">.</span>text<span class="token punctuation">.</span>Editable
<span class="token keyword">import</span> your<span class="token punctuation">.</span>package<span class="token punctuation">.</span>databinding<span class="token punctuation">.</span>ActivityMyBinding

<span class="token keyword">class</span> MyActivity <span class="token operator">:</span> <span class="token function">Activity</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
	
	<span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token operator">:</span> Bundle<span class="token operator">?</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
		<span class="token keyword">super</span><span class="token punctuation">.</span><span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token punctuation">)</span>
		
		<span class="token keyword">val</span> binding <span class="token operator">=</span> ActivityMyBinding<span class="token punctuation">.</span><span class="token function">inflate</span><span class="token punctuation">(</span>layoutInflater<span class="token punctuation">)</span>
		
		<span class="token function">setContentView</span><span class="token punctuation">(</span>binding<span class="token punctuation">.</span>root<span class="token punctuation">)</span>
		
		binding<span class="token punctuation">.</span>theEditText<span class="token punctuation">.</span><span class="token function">addTextChangedListener</span><span class="token punctuation">(</span><span class="token keyword">object</span> <span class="token operator">:</span> TextWatcher <span class="token punctuation">{</span>
			
			<span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onTextChanged</span><span class="token punctuation">(</span>s<span class="token operator">:</span> CharSequence<span class="token punctuation">,</span> start<span class="token operator">:</span> Int<span class="token punctuation">,</span> before<span class="token operator">:</span> Int<span class="token punctuation">,</span> count<span class="token operator">:</span> Int<span class="token punctuation">)</span><span class="token punctuation">{</span> <span class="token punctuation">}</span>
			<span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">beforeTextChanged</span><span class="token punctuation">(</span>s<span class="token operator">:</span> CharSequence<span class="token punctuation">,</span> start<span class="token operator">:</span> Int<span class="token punctuation">,</span> count<span class="token operator">:</span> Int<span class="token punctuation">,</span> after<span class="token operator">:</span> Int<span class="token punctuation">)</span><span class="token punctuation">{</span> <span class="token punctuation">}</span>
			
			<span class="token comment">// The s parameter is in this case the same as binding.theEditText.text.</span>
			<span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">afterTextChanged</span><span class="token punctuation">(</span>s<span class="token operator">:</span> Editable<span class="token punctuation">)</span> <span class="token punctuation">{</span>
					binding<span class="token punctuation">.</span>theTextView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"Text: <span class="token interpolation"><span class="token delimiter variable">${</span>s<span class="token punctuation">.</span><span class="token function">toString</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token delimiter variable">}</span></span>"</span>
			<span class="token punctuation">}</span>
			
		<span class="token punctuation">}</span><span class="token punctuation">)</span>
		
	<span class="token punctuation">}</span>
	
<span class="token punctuation">}</span>
